<template>
    <div class="systemUser">
        <el-button type="primary" class="btn" @click="addUser">新增用户</el-button>
        <system-usertable @dialogState="dialogState"/>
        <system-useradd v-if="state" :header="title" :data="data" @dialogState="dialogState"/>
    </div>
</template>
<script>
import SystemUsertable from './controls/systemUsertable'
import SystemUseradd from './controls/systemUseradd'

export default {
  name: 'systemUser',
  data() {
    return {
      title:'',
      data:'',
      state: false
    }
  },
  methods: {
    dialogState(data){
      data = data || {};
      this.state = data.state;
      this.title = data.title || this.title;
      this.data = data.data;
    },
    addUser(){
        let data = {
            title:'新增用户',
            state: true
        }
        this.dialogState(data);
    }
  },
  components: {
    SystemUsertable,
    SystemUseradd
  }
}
</script>
<style lang="less" scoped>
.systemUser{
    .btn{
        margin-left:20px;
    }
}
</style>
